import React, { useState, useEffect } from "react";
import SignInModal from "./SignIn";
import SignUpModal from "./SignUp";
import { Link } from "react-router-dom";

const Header = () => {
  const [isSignInOpen, setIsSignInOpen] = useState(false);
  const [isSignUpOpen, setIsSignUpOpen] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userName, setUserName] = useState("");
  const [userType, setUserType] = useState(null); // Added userType state

  useEffect(() => {
    if (isLoggedIn) {
      // Fetch userType from localStorage
      const storedUserType = localStorage.getItem("userType");
      if (storedUserType) {
        setUserType(storedUserType);
      }
    } else {
      setUserType(null); // Reset userType on logout
    }
  }, [isLoggedIn]);

  const handleSignInClick = () => {
    setIsSignInOpen(true);
  };

  const handleSignUpClick = () => {
    setIsSignUpOpen(true);
  };

  const handleLogout = () => {
    setIsLoggedIn(false);
    setUserName("");
    setUserType(null); // Clear userType on logout
    localStorage.removeItem("userId"); // Remove user ID from local storage
    localStorage.removeItem("userType"); // Remove userType from local storage
  };

  return (
    <>
      <header className="header">
        <Link to={"/"}>
          <div className="logo">
            <img
              src="https://res.cloudinary.com/dlxfub7s1/image/upload/v1734863215/jihc-logo-v1_xr5t1z.png"
              alt="JIHC Events Logo"
            />
          </div>
        </Link>
        <nav className="nav-menu">
          <Link to="/" className="nav-link">
            Home
          </Link>
          <Link to="/events" className="nav-link">
            Events
          </Link>
          <Link to="/about" className="nav-link">
            About Us
          </Link>
          <Link to="/contact" className="nav-link">
            Contact Us
          </Link>
          {isLoggedIn && userType === "Teacher" && (
            <Link to="/create-event" className="nav-link">
              Create Event
            </Link>
          )}
        </nav>
        <div className="auth-buttons">
          {!isLoggedIn ? (
            <>
              <button onClick={handleSignInClick} className="btn sign-in-btn">
                Sign In
              </button>
              <button onClick={handleSignUpClick} className="btn sign-up-btn">
                Sign Up
              </button>
            </>
          ) : (
            <>
              <Link to="/profile" className="btn profile-btn">
                Profile
              </Link>
              <button onClick={handleLogout} className="btn logout-btn">
                Logout
              </button>
            </>
          )}
        </div>
        {/* Sign In Modal */}
        {isSignInOpen && (
          <SignInModal
            onClose={() => setIsSignInOpen(false)}
            setIsLoggedIn={setIsLoggedIn}
            setUserName={setUserName}
          />
        )}
        {/* Sign Up Modal */}
        {isSignUpOpen && (
          <SignUpModal
            onClose={() => setIsSignUpOpen(false)}
            setIsLoggedIn={setIsLoggedIn}
            setUserName={setUserName}
          />
        )}
      </header>
    </>
  );
};

export default Header;
