// AboutUs.js
import React, { useState } from "react";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCalendar,
  faUsers,
  faTools,
} from "@fortawesome/free-solid-svg-icons";
import { faFacebook, faInstagram } from "@fortawesome/free-brands-svg-icons";
import {
  FaCalendarCheck,
  FaUsers,
  FaChartLine,
  FaTrophy,
} from "react-icons/fa";

const About = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const items = [
    {
      icon: <FaCalendarCheck />,
      title: "Organized Events",
      description: "Seamlessly manage and organize events for everyone.",
    },
    {
      icon: <FaUsers />,
      title: "Community Building",
      description: "Foster connections and build a stronger community.",
    },
    {
      icon: <FaChartLine />,
      title: "Growth Opportunities",
      description: "Unlock personal and professional growth opportunities.",
    },
    {
      icon: <FaTrophy />,
      title: "Recognitions",
      description: "Celebrate achievements with recognition and rewards.",
    },
  ];
  const nextSlide = () => {
    setActiveIndex((prevIndex) => (prevIndex + 1) % items.length);
  };

  const prevSlide = () => {
    setActiveIndex(
      (prevIndex) => (prevIndex - 1 + items.length) % items.length
    );
  };

  return (
    <div className="about-us">
      {/* Header Section */}
      <section className="header-section">
        <h1>About JIHC Events</h1>
        <p>Discover how we build connections and inspire through events.</p>
        <div className="underline"></div>
      </section>
      {/* Why Choose Us Section */}
      <div className="why-choose-us-slider">
        <button className="slider-button prev" onClick={prevSlide}>
          {"<"}
        </button>
        <div className="slider-container">
          {items.map((item, index) => {
            const offset = index - activeIndex;
            const isCenter = offset === 0;

            return (
              <div
                key={index}
                className={`slider-item ${isCenter ? "center" : ""}`}
                style={{
                  transform: `translateX(${offset * 50}%) scale(${
                    isCenter ? 1 : 0.8
                  })`,
                  zIndex: isCenter ? 2 : 1,
                  opacity: isCenter ? 1 : 0.6,
                }}
              >
                <div className="icon">{item.icon}</div>
                <h3 className="title">{item.title}</h3>
                <p className="description">{item.description}</p>
              </div>
            );
          })}
        </div>
        <button className="slider-button next" onClick={nextSlide}>
          {">"}
        </button>
      </div>

      {/* Mission & Vision Section */}
      {/* Mission and Vision Section */}
      <div className="mission-vision-section">
        <div className="mission-container">
          <div className="mission-box">
            <h3>Our Mission</h3>
            <p>
              To inspire students and organizers by creating opportunities for
              learning, networking, and collaboration through impactful events.
            </p>
          </div>
          <div className="mission-image">
            <img
              src="https://res.cloudinary.com/dlxfub7s1/image/upload/v1736717765/154657e5b8393753a45bdd2f307b2598_vzvd4s.jpg"
              alt="Mission Visual"
            />
          </div>
        </div>
        <div className="vision-container">
          <div className="vision-image">
            <img
              src="https://res.cloudinary.com/dlxfub7s1/image/upload/v1736717765/60ab479cb8f5b6e91ecfd28e3ef4a5f4_jdva9z.jpg"
              alt="Vision Visual"
            />
          </div>
          <div className="vision-box">
            <h3>Our Vision</h3>
            <p>
              To be a leading platform for event management, empowering students
              and teachers to create memorable and impactful experiences.
            </p>
          </div>
        </div>
      </div>
      {/* Testimonials Section */}
      {/* Reviews Section */}
      {/* Reviews Section */}
      <div className="puzzle-reviews-section">
        <h2 className="reviews-title">What People Say</h2>
        <div className="puzzle-reviews-container">
          <div className="puzzle-piece top-left">
            <p>
              "JIHC Events transformed the way we organize and participate in
              events. It's a game-changer!"
            </p>
            <span>- Moldir, Student </span>
          </div>
          <div className="puzzle-piece top-right">
            <p>
              "The tools and community built around this platform are
              incredible. Highly recommend it."
            </p>
            <span>- Azina, Student </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
