import React, { useState, useEffect } from "react";

const HomePage = () => {
  const [events, setEvents] = useState([]);
  const [filteredEvents, setFilteredEvents] = useState([]);
  const [registeredEvents, setRegisteredEvents] = useState(() => {
    const storedEvents = localStorage.getItem("registeredEvents");
    return storedEvents ? JSON.parse(storedEvents) : [];
  });
  const [searchParams, setSearchParams] = useState({
    title: "",
    type: "",
    location: "",
    date: "",
  });
  const [selectedCategory, setSelectedCategory] = useState("");
  const [selectedDateFilter, setSelectedDateFilter] = useState("");
  const [searchFilteredEvents, setSearchFilteredEvents] = useState([]);
  const [dateFilteredEvents, setDateFilteredEvents] = useState([]);
  const [isDateFilterActive, setIsDateFilterActive] = useState(false);
  const eventTypes = [
    "Competition",
    "Event",
    "Networking",
    "Seminar",
    "Sports",
  ];
  const locations = [
    "ACT Hall",
    "Library",
    "Library Auditorium",
    "Sports Hall",
    "Techolab",
  ];
  const [dateFilter, setDateFilter] = useState("Today");

  // Fetch all events on page load
  useEffect(() => {
    fetch(
      "https://jihceventsapi-hpdng8h8e7eqhdbm.canadacentral-01.azurewebsites.net/api/events"
    )
      .then((response) => response.json())
      .then((data) => {
        setEvents(data);
        setFilteredEvents(data);
      })
      .catch((error) => console.error("Error fetching events:", error));
  }, []);

  // Handle input change for search

  // Handle search functionality
  const handleSearch = () => {
    const filtered = events.filter((event) => {
      const matchesTitle =
        searchParams.title === "" ||
        event.title.toLowerCase().includes(searchParams.title.toLowerCase());
      const matchesType =
        searchParams.type === "" || event.type === searchParams.type;
      const matchesLocation =
        searchParams.location === "" ||
        event.location === searchParams.location;
      const matchesDate =
        searchParams.date === "" || event.date === searchParams.date;

      return matchesTitle && matchesType && matchesLocation && matchesDate;
      setSearchFilteredEvents(filtered);
      setIsDateFilterActive(false);
    });

    setFilteredEvents(filtered);
  };
  const handleDateFilter = (filter) => {
    const today = new Date();
    const filtered = events.filter((event) => {
      const eventDate = new Date(event.date);
      if (filter === "Today") {
        return eventDate.toDateString() === today.toDateString();
      }
      if (filter === "Tomorrow") {
        const tomorrow = new Date(today);
        tomorrow.setDate(today.getDate() + 1);
        return eventDate.toDateString() === tomorrow.toDateString();
      }
      if (filter === "This Week") {
        const weekStart = new Date(today);
        weekStart.setDate(today.getDate() - today.getDay() + 1); // Start of the week (Monday)
        const weekEnd = new Date(weekStart);
        weekEnd.setDate(weekStart.getDate() + 6); // End of the week (Sunday)
        return eventDate >= weekStart && eventDate <= weekEnd;
      }
      return false;
    });

    setDateFilteredEvents(filtered);
    setIsDateFilterActive(true); // Enable date filter when a date is selected
  };

  // Handle Input Changes for Search
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setSearchParams((prevParams) => ({
      ...prevParams,
      [name]: value,
    }));
  };
  // Handle event registration
  const handleRegister = async (eventId) => {
    const userId = localStorage.getItem("userId"); // Check if the user is logged in

    if (!userId) {
      alert("You need to sign in to register for this event.");
      return;
    }

    try {
      const response = await fetch(
        "https://jihceventsapi-hpdng8h8e7eqhdbm.canadacentral-01.azurewebsites.net/api/eventregistrations",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ eventId, userId }),
        }
      );

      if (response.ok) {
        setRegisteredEvents((prev) => [...prev, eventId]); // Update the registered events list
        alert("You have successfully registered for the event!");
      } else {
        const errorData = await response.json();
        alert(errorData.message || "An error occurred.");
      }
    } catch (error) {
      console.error("Error registering for the event:", error);
      alert("Something went wrong. Please try again.");
    }
  };

  // Filter by category or date
  useEffect(() => {
    let filtered = [...events];
    if (selectedCategory) {
      filtered = filtered.filter((event) => event.type === selectedCategory);
    }
    if (selectedDateFilter) {
      const today = new Date();
      filtered = filtered.filter((event) => {
        const eventDate = new Date(event.date);
        if (selectedDateFilter === "Today") {
          return eventDate.toDateString() === today.toDateString();
        }
        if (selectedDateFilter === "Tomorrow") {
          const tomorrow = new Date(today);
          tomorrow.setDate(today.getDate() + 1);
          return eventDate.toDateString() === tomorrow.toDateString();
        }
        if (selectedDateFilter === "This Weekend") {
          const weekendStart = new Date(today);
          weekendStart.setDate(today.getDate() + (6 - today.getDay()));
          const weekendEnd = new Date(weekendStart);
          weekendEnd.setDate(weekendStart.getDate() + 1);
          return eventDate >= weekendStart && eventDate <= weekendEnd;
        }
        return true;
      });
    }
    setFilteredEvents(filtered);
  }, [selectedCategory, selectedDateFilter, events]);
  // Horizontal Scroll Handlers
  const scrollLeft = () => {
    document.querySelector(".student-events-scroll").scrollBy({
      left: -300,
      behavior: "smooth",
    });
  };

  const scrollRight = () => {
    document.querySelector(".student-events-scroll").scrollBy({
      left: 300,
      behavior: "smooth",
    });
  };

  return (
    <>
      {/* Hero Section */}
      <div className="hero">
        <h1 className="hero-title">Welcome to JIHC Events</h1>
        <p className="hero-subtitle">Discover, learn, and connect.</p>
      </div>

      <div className="search-section">
        <input
          type="text"
          name="title"
          value={searchParams.title}
          onChange={handleInputChange}
          placeholder="Enter event name"
          className="search-input"
        />
        <select
          name="type"
          value={searchParams.type}
          onChange={handleInputChange}
          className="filter-dropdown"
        >
          <option value="">Select Event Type</option>
          {eventTypes.map((type, index) => (
            <option key={index} value={type}>
              {type}
            </option>
          ))}
        </select>
        <select
          name="location"
          value={searchParams.location}
          onChange={handleInputChange}
          className="filter-dropdown"
        >
          <option value="">Select Location</option>
          {locations.map((location, index) => (
            <option key={index} value={location}>
              {location}
            </option>
          ))}
        </select>
        <input
          type="date"
          name="date"
          value={searchParams.date}
          onChange={handleInputChange}
          className="search-input"
        />
        <button onClick={handleSearch} className="search-btn">
          Search
        </button>
      </div>

      <div className="filtered-search-events">
        {filteredEvents.length > 0 ? (
          <div className="events-container">
            {filteredEvents.map((event) => {
              const isRegistered = registeredEvents.includes(event.id);
              const progressPercent =
                (event.attendees / event.maxAttendees) * 100;

              return (
                <div key={event.id} className="event-card">
                  <img
                    src={event.image}
                    alt={event.title}
                    className="event-image"
                  />
                  <div className="image-badge">{event.price}</div>
                  <div className="event-details">
                    <h3 className="event-title">{event.title}</h3>
                    <div className="event-inline">
                      <div className="event-date">
                        <i className="fa fa-calendar" aria-hidden="true"></i>
                        {event.date}
                      </div>
                      <div className="event-location">
                        <i className="fa fa-map-marker" aria-hidden="true"></i>
                        {event.location}
                      </div>
                    </div>
                    <div className="event-audience">{event.audience}</div>
                    <div className="progress-bar-container">
                      <div
                        className="progress-fill"
                        style={{
                          width: `${progressPercent}%`,
                        }}
                      ></div>
                    </div>
                    <p className="progress-info">
                      {event.attendees}/{event.maxAttendees} Attendees
                    </p>
                    {isRegistered ? (
                      <button className="register-button registered" disabled>
                        Registered
                      </button>
                    ) : (
                      <button
                        className="register-button"
                        onClick={() => handleRegister(event.id)}
                        disabled={
                          !localStorage.getItem("userId") ||
                          event.attendees >= event.maxAttendees
                        }
                      >
                        {event.attendees >= event.maxAttendees
                          ? "Event Full"
                          : "Register Now"}
                      </button>
                    )}
                  </div>
                </div>
              );
            })}
          </div>
        ) : (
          <p className="no-events-message">No events found for your search.</p>
        )}
      </div>

      {/* Category Filter */}
      <div className="category-filter">
        {eventTypes.map(({ type, image }) => (
          <div
            key={type}
            className="category-circle"
            onClick={() =>
              setFilteredEvents(events.filter((event) => event.type === type))
            }
          >
            <img src={image} alt={type} className="category-image" />
            <p className="category-title">{type}</p>
          </div>
        ))}
      </div>

      {/* Date Filter */}
      {/* Date Filter */}
      {/* Date Filter Section */}
      <div className="date-filter-section">
        {/* Date Filter Section */}
        <h2 className="section-title">Filter by Date</h2>
        <div className="date-filter">
          {["Today", "Tomorrow", "This Week"].map((dateFilter) => (
            <button
              key={dateFilter}
              className="date-button"
              onClick={() => handleDateFilter(dateFilter)}
            >
              {dateFilter}
            </button>
          ))}
        </div>
        {/* Render Filtered Events */}
        <div className="filtered-events-section">
          <div className="events-container">
            {(isDateFilterActive
              ? dateFilteredEvents
              : searchFilteredEvents
            ).map((event) => {
              const isRegistered = registeredEvents.includes(event.id);
              const progressPercent =
                (event.attendees / event.maxAttendees) * 100;

              return (
                <div key={event.id} className="event-card">
                  <img
                    src={event.image}
                    alt={event.title}
                    className="event-image"
                  />
                  <div className="image-badge">{event.price}</div>
                  <div className="event-details">
                    <h3 className="event-title">{event.title}</h3>
                    <div className="event-inline">
                      <div className="event-date">
                        <i className="fa fa-calendar" aria-hidden="true"></i>
                        {event.date}
                      </div>
                      <div className="event-location">
                        <i className="fa fa-map-marker" aria-hidden="true"></i>
                        {event.location}
                      </div>
                    </div>
                    <div className="event-audience">{event.audience}</div>
                    <div className="progress-bar-container">
                      <div
                        className="progress-fill"
                        style={{
                          width: `${progressPercent}%`,
                        }}
                      ></div>
                    </div>
                    <p className="progress-info">
                      {event.attendees}/{event.maxAttendees} Attendees
                    </p>
                    {isRegistered ? (
                      <button className="register-button registered" disabled>
                        Registered
                      </button>
                    ) : (
                      <button
                        className="register-button"
                        onClick={() => handleRegister(event.id)}
                        disabled={
                          !localStorage.getItem("userId") ||
                          event.attendees >= event.maxAttendees
                        }
                      >
                        {event.attendees >= event.maxAttendees
                          ? "Event Full"
                          : "Register Now"}
                      </button>
                    )}
                  </div>
                </div>
              );
            })}
          </div>
          : (
          {(isDateFilterActive ? dateFilteredEvents : searchFilteredEvents)
            .length === 0 && (
            <p className="no-events-message">No events found.</p>
          )}
          )
        </div>
      </div>

      {/* Render Filtered Events */}

      {/* Popular Events in ACT Hall */}
      {/* Popular Events in ACT Hall */}
      <div className="popular-events-section">
        <h2 className="section-title">
          Popular Events in ACT Hall
          <div className="section-underline"></div>
        </h2>
        <div className="scroll-buttons-container">
          <button
            className="scroll-button left-scroll"
            onClick={() => {
              document.querySelector(".popular-events-scroll").scrollBy({
                left: -300,
                behavior: "smooth",
              });
            }}
          >
            &lt;
          </button>
          <button
            className="scroll-button right-scroll"
            onClick={() => {
              document.querySelector(".popular-events-scroll").scrollBy({
                left: 300,
                behavior: "smooth",
              });
            }}
          >
            &gt;
          </button>
        </div>

        <div className="popular-events-scroll">
          {filteredEvents
            .filter((event) => event.location === "ACT Hall")
            .map((event) => {
              const isRegistered = registeredEvents.includes(event.id);
              const progressPercent =
                (event.attendees / event.maxAttendees) * 100;

              return (
                <div key={event.id} className="event-card">
                  <img
                    src={event.image}
                    alt={event.title}
                    className="event-image"
                  />
                  <div className="image-badge">{event.price}</div>
                  <div className="event-details">
                    <h3 className="event-title">{event.title}</h3>
                    <div className="event-inline">
                      <div className="event-date">{event.date}</div>
                      <div className="event-location">{event.location}</div>
                    </div>
                    <div className="progress-bar-container">
                      <div
                        className="progress-fill"
                        style={{
                          width: `${progressPercent}%`,
                        }}
                      ></div>
                    </div>
                    <p className="progress-info">
                      {event.attendees}/{event.maxAttendees} Attendees
                    </p>
                    {isRegistered ? (
                      <button className="register-button registered" disabled>
                        Registered
                      </button>
                    ) : (
                      <button
                        className="register-button"
                        onClick={() => handleRegister(event.id)}
                        disabled={
                          !localStorage.getItem("userId") ||
                          event.attendees >= event.maxAttendees
                        }
                      >
                        {event.attendees >= event.maxAttendees
                          ? "Event Full"
                          : "Register Now"}
                      </button>
                    )}
                  </div>
                </div>
              );
            })}
        </div>
      </div>

      {/* Events Only for Students */}
      <div className="student-events-section">
        <h2 className="section-title">
          Events for Students
          <div className="section-underline"></div>
        </h2>
        <div className="student-events-container">
          {filteredEvents
            .filter((event) => event.audience === "Student")
            .map((event) => {
              const isRegistered = registeredEvents.includes(event.id);
              const progressPercent =
                (event.attendees / event.maxAttendees) * 100;

              return (
                <div key={event.id} className="event-card">
                  <img
                    src={event.image}
                    alt={event.title}
                    className="event-image"
                  />
                  <div className="image-badge">{event.price}</div>
                  <div className="event-details">
                    <h3 className="event-title">{event.title}</h3>
                    <div className="event-inline">
                      <div className="event-date">
                        <i className="fa fa-calendar" aria-hidden="true"></i>
                        {event.date}
                      </div>
                      <div className="event-location">
                        <i className="fa fa-map-marker" aria-hidden="true"></i>
                        {event.location}
                      </div>
                    </div>
                    <div className="event-audience">{event.audience}</div>
                    <div className="progress-bar-container">
                      <div
                        className="progress-fill"
                        style={{
                          width: `${progressPercent}%`,
                        }}
                      ></div>
                    </div>
                    <p className="progress-info">
                      {event.attendees}/{event.maxAttendees} Attendees
                    </p>
                    {isRegistered ? (
                      <button className="register-button registered" disabled>
                        Registered
                      </button>
                    ) : (
                      <button
                        className="register-button"
                        onClick={() => handleRegister(event.id)}
                        disabled={
                          !localStorage.getItem("userId") ||
                          event.attendees >= event.maxAttendees
                        }
                      >
                        {event.attendees >= event.maxAttendees
                          ? "Event Full"
                          : "Register Now"}
                      </button>
                    )}
                  </div>
                </div>
              );
            })}
        </div>
      </div>

      {/* All Events */}
      <div className="events-container">
        <h2 className="section-title">
          More Events
          <div className="section-underline"></div>
        </h2>
        {filteredEvents.map((event) => {
          const isRegistered = registeredEvents.includes(event.id);
          const progressPercent = (event.attendees / event.maxAttendees) * 100;

          return (
            <div key={event.id} className="event-card">
              <img
                src={event.image}
                alt={event.title}
                className="event-image"
              />
              <div className="image-badge">{event.price}</div>
              <div className="event-details">
                <h3 className="event-title">{event.title}</h3>
                <div className="event-inline">
                  <div className="event-date">
                    <i className="fa fa-calendar" aria-hidden="true"></i>
                    {event.date}
                  </div>
                  <div className="event-location">
                    <i className="fa fa-map-marker" aria-hidden="true"></i>
                    {event.location}
                  </div>
                </div>
                <div className="event-audience">{event.audience}</div>
                <div className="progress-bar-container">
                  <div
                    className="progress-fill"
                    style={{
                      width: `${progressPercent}%`,
                    }}
                  ></div>
                </div>
                <p className="progress-info">
                  {event.attendees}/{event.maxAttendees} Attendees
                </p>
                {isRegistered ? (
                  <button className="register-button registered" disabled>
                    Registered
                  </button>
                ) : (
                  <button
                    className="register-button"
                    onClick={() => handleRegister(event.id)}
                    disabled={
                      !localStorage.getItem("userId") ||
                      event.attendees >= event.maxAttendees
                    }
                  >
                    {event.attendees >= event.maxAttendees
                      ? "Event Full"
                      : "Register Now"}
                  </button>
                )}
              </div>
            </div>
          );
        })}
      </div>
    </>
  );
};

export default HomePage;
