import React, { useRef } from "react";
import emailjs from "emailjs-com";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEnvelope,
  faPhone,
  faMapMarkerAlt,
} from "@fortawesome/free-solid-svg-icons";
import { faInstagram, faFacebook } from "@fortawesome/free-brands-svg-icons";

const ContactUs = () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();
    emailjs
      .sendForm(
        "service_g6whaij",
        "template_j0ctme9",
        form.current,
        "E5iW-kR7EcirveQHC"
      )
      .then(
        (result) => {
          alert("Message sent successfully!");
          e.target.reset();
        },
        (error) => {
          alert("An error occurred. Please try again.");
        }
      );
  };

  return (
    <div className="contact-section">
      {/* Contact Form */}
      <div className="contact-form">
        <h1 className="contact-title">Contact Us</h1>
        <p className="contact-description">
          We'd love to hear from you! Please fill out the form below or visit us
          at our address.
        </p>
        <form ref={form} onSubmit={sendEmail}>
          <input
            type="text"
            name="firstName"
            placeholder="First Name*"
            required
          />
          <input
            type="text"
            name="lastName"
            placeholder="Last Name*"
            required
          />
          <input type="email" name="email" placeholder="Email*" required />
          <textarea name="message" rows="5" placeholder="Message*" required />
          <button type="submit" className="submit-btn">
            Send Message
          </button>
        </form>
        <div className="contact-details">
          <div className="contact-item">
            <FontAwesomeIcon icon={faEnvelope} className="contact-icon" />
            <a href="mailto:info@jihc.kz" className="contact-text">
              info@jihc.kz
            </a>
          </div>
          <div className="contact-item">
            <FontAwesomeIcon icon={faPhone} className="contact-icon" />
            <a href="tel:87078020088" className="contact-text">
              8-707-802-00-88
            </a>
          </div>
          <div className="contact-item">
            <FontAwesomeIcon icon={faMapMarkerAlt} className="contact-icon" />
            <span className="contact-text">, . , 154</span>
          </div>
        </div>
      </div>

      {/* Contact Info and Map */}

      <div className="contact-info">
        <div className="map-container">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d46763.522386370154!2d71.33593595381794!3d42.899850827140625!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x38a703484d578eb5%3A0xbb36b3fc4ad3ccd2!2z0JbQkNCc0JHQq9CbINCY0J3QndCe0JLQkNCm0JjQr9Cb0KvSmiDQltCe0pLQkNCg0Ksg0JrQntCb0JvQldCU0JbQhg!5e0!3m2!1sru!2skz!4v1736695792761!5m2!1sru!2skz"
            width="100%"
            height="450"
            style={{ border: 0 }}
            allowFullScreen=""
            loading="lazy"
            referrerPolicy="no-referrer-when-downgrade"
          ></iframe>
        </div>
      </div>

      {/* Map Section */}
    </div>
  );
};

export default ContactUs;
