import React, { useState, useEffect } from "react";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";

const EventsPage = () => {
  const [events, setEvents] = useState([]);
  const [filteredEvents, setFilteredEvents] = useState([]);
  const [searchParams, setSearchParams] = useState({
    title: "",
    type: "",
    location: "",
  });
  const [selectedCategory, setSelectedCategory] = useState("");
  const [selectedDate, setSelectedDate] = useState(null);

  const eventTypes = [
    "Competition",
    "Event",
    "Networking",
    "Seminar",
    "Sports",
  ];
  const locations = [
    "ACT Hall",
    "Library",
    "Library Auditorium",
    "Sports Hall",
    "Techolab",
  ];

  useEffect(() => {
    fetch(
      "https://jihceventsapi-hpdng8h8e7eqhdbm.canadacentral-01.azurewebsites.net/api/events"
    )
      .then((response) => response.json())
      .then((data) => {
        setEvents(data);
        setFilteredEvents(data);
      })
      .catch((error) => console.error("Error fetching events:", error));
  }, []);

  const handleDateChange = (date) => {
    setSelectedDate(date);
    const filtered = events.filter((event) => {
      const eventDate = new Date(event.date);
      return eventDate.toDateString() === date.toDateString();
    });
    setFilteredEvents(filtered);
  };

  const handleCategoryChange = (category) => {
    setSelectedCategory(category);
    const filtered = events.filter((event) => event.type === category);
    setFilteredEvents(filtered);
  };

  const handleSearch = () => {
    const filtered = events.filter((event) => {
      const matchesTitle =
        searchParams.title === "" ||
        event.title.toLowerCase().includes(searchParams.title.toLowerCase());
      const matchesType =
        searchParams.type === "" || event.type === searchParams.type;
      const matchesLocation =
        searchParams.location === "" ||
        event.location === searchParams.location;

      return matchesTitle && matchesType && matchesLocation;
    });

    setFilteredEvents(filtered);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setSearchParams((prev) => ({ ...prev, [name]: value }));
  };

  const resetFilters = () => {
    setFilteredEvents(events);
    setSearchParams({ title: "", type: "", location: "" });
    setSelectedCategory("");
    setSelectedDate(null);
  };

  return (
    <div className="events-page">
      <h1 className="page-title">Events</h1>

      <div className="filters-container">
        <div className="calendar-container">
          <h3>Select Date</h3>
          <Calendar onChange={handleDateChange} value={selectedDate} />
        </div>

        <div className="category-filter">
          <h3>Categories</h3>
          <ul>
            {eventTypes.map((type) => (
              <li
                key={type}
                className={selectedCategory === type ? "active" : ""}
                onClick={() => handleCategoryChange(type)}
              >
                {type}
              </li>
            ))}
          </ul>
        </div>

        <div className="search-section">
          <input
            type="text"
            name="title"
            value={searchParams.title}
            onChange={handleInputChange}
            placeholder="Enter event name"
          />
          <select
            name="type"
            value={searchParams.type}
            onChange={handleInputChange}
          >
            <option value="">Select Event Type</option>
            {eventTypes.map((type) => (
              <option key={type} value={type}>
                {type}
              </option>
            ))}
          </select>
          <select
            name="location"
            value={searchParams.location}
            onChange={handleInputChange}
          >
            <option value="">Select Location</option>
            {locations.map((location) => (
              <option key={location} value={location}>
                {location}
              </option>
            ))}
          </select>
          <button className="filter-button" onClick={handleSearch}>
            Search
          </button>
          <button className="filter-button reset" onClick={resetFilters}>
            Reset
          </button>
        </div>
      </div>

      <div className="events-container">
        {filteredEvents.length > 0 ? (
          filteredEvents.map((event) => (
            <div key={event.id} className="event-card">
              <img
                src={event.image}
                alt={event.title}
                className="event-image"
              />
              <div className="event-details">
                <h3 className="event-title">{event.title}</h3>
                <p className="event-date">{event.date}</p>
                <p className="event-location">{event.location}</p>
                <p className="event-description">{event.description}</p>
              </div>
            </div>
          ))
        ) : (
          <p>No events found matching your criteria.</p>
        )}
      </div>
    </div>
  );
};

export default EventsPage;
